body {
  background-image: url("./images/app.background.png");
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

nav {
  width: 100%;
  display: flex;
  margin: 0px;
  background: #1a0020;
  box-shadow: 0px 0px 10px 0px #0000003d;
  padding: 10px;
}

nav ul {
  width: auto;
  margin: auto;
  padding: 0px;
}

nav ul li {
  display: inline-flex;
  padding: 10px;
  min-width: 85px;
  font-size: 15px;
  border: 2px solid white;
  margin: auto 5px;
  border-radius: 10px;
  background: rgb(225,1,255);
  background: linear-gradient( 
90deg, rgba(225,1,255,1) 0%, rgba(251,0,203,1) 0%, rgba(237,0,121,1) 100%);
}

nav ul li:hover {
  box-shadow: 0px 5px 15px 0px #00000070;
  transition: 0.2s ease-in-out;
  background: rgb(2,0,36);
  background: linear-gradient(
90deg, rgba(2,0,36,1) 0%, rgba(209,209,209,1) 0%, rgba(232,232,232,1) 100%);
}

nav ul li:hover a {
  color:black;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  width: 100%;
  text-align: center;
}

button.Default-Anchor-Button {
  padding: 15px;
  min-width: 200px;
  color: white;
  font-weight: bold;
  font-size: 15px;
  border-radius: 15px;
  border: 2px solid white;
  background: rgb(225,1,255);
  background: linear-gradient(
90deg, rgba(225,1,255,1) 0%, rgba(251,0,203,1) 0%, rgba(237,0,121,1) 100%);
}

button.Default-Anchor-Button:hover {
  cursor:pointer;
  color:black;
  box-shadow: 0px 5px 15px 0px #00000070;
  transition: 0.2s ease-in-out;
  background: rgb(2,0,36);
  background: linear-gradient( 
90deg, rgba(2,0,36,1) 0%, rgba(209,209,209,1) 0%, rgba(232,232,232,1) 100%);
}

button.Default-Action-Button {
  padding: 10px;
  min-width: 115px;
  color: white;
  font-weight: bold;
  font-size: 12px;
  border-radius: 0;
  border: 2px solid white;
  background: rgb(225,1,255);
  background: linear-gradient(90deg, rgb(52 1 255) 0%, rgb(102 0 82) 0%, rgb(50 0 58) 100%);
}

button.Default-Action-Button:hover {
  cursor:pointer;
  color:black;
  box-shadow: 0px 5px 15px 0px #00000070;
  transition: 0.2s ease-in-out;
  background: rgb(2,0,36);
  background: linear-gradient( 
90deg, rgba(2,0,36,1) 0%, rgba(209,209,209,1) 0%, rgba(232,232,232,1) 100%);
}

.Default-Form-Theme {
  font-size: 2.0rem;
  padding: 0px 25px 25px 25px;
  background: #2b003cc7;
  margin: 200px auto 50px auto;
  min-width: 80%;
  min-height: 250px;
  border-radius: 10px;
  box-shadow: 10px 30px 40px 5px #00000069;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
  backdrop-filter: blur(10px);
}

.Default-Form-Theme {
  padding: 15px;
  width: 70%;
  margin: auto;
  border: 1px solid green;
}

.Default-Form-Theme {
  font-size: 2.0rem;
  padding: 25px;
  background: #2b003cc7;
  margin: 300px auto 50px auto;
  min-width: 80%;
  min-height: 250px;
  border-radius: 50px;
  box-shadow: 10px 30px 40px 5px #00000069;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
  backdrop-filter: blur(10px);
}

.Default-Input-Wraper label {
  font-size: 20px;
  font-weight: 300;
  padding: 20px;
}

.Default-Input-Wraper input.Default-Input {
  padding: 10px;
  background: #ffffff;
  border: 2px solid #3b013f;
  border-radius: 5px;
}